.post {
    border: 1px solid #474747;
    margin-bottom: 30px;
    width: 1000px;
    letter-spacing: 1px;
}

.post__meta {
    background-color: #2f6ecc;
    padding: 8px 10px;
}
.post__meta, .post__meta a {
    color: #fff;
}
.post__meta a {
    text-decoration: none;
}
.post__meta a:hover {
    text-decoration: underline;
}
.post__meta a:focus {
    outline: 3px solid #fff;
}

.post__title {
    margin: 0;
}

.post__container {
    padding: 10px;
}

.post__body {
    margin: 10px 0;
}

.post__actions {
    padding: 0 0 10px 10px;
}

.post__actions button.post__commentsButton {
    border: 0;
    padding: 10px;
    cursor: pointer;
    background-color: rgb(237 238 242);
    border: 1px solid #ccc;
    color: #333;
}
.post__actions button.post__commentsButton:hover {
    padding: 10px;
    cursor: pointer;
    border-color: #333;
}
.post__actions button.post__commentsButton:focus {
    outline: 3px solid #7b9ee9;
    border-color: transparent;
}

.post__comments {
    border-top: 1px solid #474747;
    padding: 10px;
}
.post__comments div {
    padding: 10px;
}
.post__comments div:nth-of-type(even) {
    background-color: rgb(237 238 242);
}
.post__comments > div > p {
    margin: 0;
}
.post__comments p:nth-of-type(odd) {
    font-weight: bold;
}