.users {
    border-collapse: collapse;
    border: 1px solid #474747;
    letter-spacing: 1px;
    max-width: calc(100vw - 50%)
}

.users thead {
    background-color: #2f6ecc;
    color: #fff;
}

.users th, td {
  border: 1px solid #474747;
  padding: 8px 10px;
}

.users tbody tr th a {
    color: #2f6ecc;
    text-decoration: none;
}
.users tbody tr th a:hover {
    text-decoration: underline;
}
.users tbody tr th a:focus {
    outline: 3px solid #7b9ee9;
}

.users td:last-of-type {
    text-align: center;
}

.users tbody > tr:nth-of-type(even) {
    background-color: rgb(237 238 242);
}

.users tfoot th {
    text-align: right;
}
  
.users tfoot td {
    font-weight: bold;
}