.user {
    border-collapse: collapse;
    border: 1px solid #474747;
    letter-spacing: 1px;
    max-width: calc(100vw - 50%)
}

.user thead {
    background-color: #2f6ecc;
    color: #fff;
}

.user th, td {
  border: 1px solid #474747;
  padding: 8px 10px;
}

.user tbody tr th a {
    color: #2f6ecc;
    text-decoration: none;
}
.user tbody tr th a:hover {
    text-decoration: underline;
}
.user tbody tr th a:focus {
    outline: 3px solid #7b9ee9;
}

.user td:last-of-type {
    text-align: center;
}

.user tbody > tr:nth-of-type(even) {
    background-color: rgb(237 238 242);
}

.user tfoot th {
    text-align: right;
}
  
.user tfoot td {
    font-weight: bold;
}