.header {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    padding: 10px;
    background-color: #eee;
}

.header__title {
    margin-right: 20px;
}
.header__title h1 {
    margin: 0;
}

.header__nav {
    display: flex;
    flex-direction: row;
}
.header__nav li {
    list-style-type: none;
    margin: 0 5px;
}
.header__nav li a {
    text-decoration: none;
    font-size: 20px;
    color: #2f6ecc;
    padding: 10px;
}
.header__nav li a:focus {
    outline: 3px solid #7b9ee9;
}
.header__nav li a.active {
    color: #fff;
    background-color: #2f6ecc;
}